import React from 'react';
import biometria from '../../../assets/icons/biometria blue.png';
import './ModalBiometria.scss';
import ModalContainer from '../ModalContainer/ModalContainer';
import {
  LoadingState,
  updateLoading,
  ModalWarning,
  updateWarning,
  SystemState
} from '../../../stores/system';
import SystemActionTypes from '../../../stores/system/system.action-types';
import { connect } from 'react-redux';
import { AppState } from '../../../configureStore';
import { PacienteState, updatePaciente } from '../../../stores/paciente';
import Axios from 'axios';
import { environment } from '../../../environment';
import PacienteActionTypes from '../../../stores/paciente/paciente.action-types';

interface BioResponse {
  identifyResult: boolean;
  template: {
    buffer: string;
    size: number;
    quality: number;
  };
  token: string;
  version: string;
}

interface BioPost {
  matricula: string;
  digital: string;
  qualidade: number;
}

interface Props {
  paciente: PacienteState;
  closeModal(): any;
  show: boolean;
  setLoadingStatus(status: LoadingState);
  updateWarning(warning: ModalWarning);
  clearPaciente();
  system: SystemState;
}

const ModalBiometria: React.FC<Props> = (props) => {
  const updateBio = async () => {
    try {
      props.setLoadingStatus(LoadingState.Loading);
      const bioResponse = await Axios.post<BioResponse>(
        environment.biometricService
      );
      if (bioResponse.data.template.quality <= 60) {
        props.updateWarning({
          text:
            'Erro ao obter a biometria. Qualidade da digital obtida é insuficiente.',
          title: 'Erro',
          type: 'error'
        });
        props.setLoadingStatus(LoadingState.Failed);
        return;
      }
      const matricula = [
        props.paciente.convenio.replace(/\D/g, '').slice(0, 3),
        props.paciente.convenio.replace(/\D/g, '').slice(3)
      ].join('.');
      const body: BioPost = {
        matricula,
        digital: bioResponse.data.template.buffer,
        qualidade: bioResponse.data.template.quality
      };
      await Axios.post(
        'https://api-athenasaude.sensedia.com/biometria-athena/v1/biometrias',
        body,
        {
          headers: {
            // eslint-disable-next-line @typescript-eslint/camelcase
            access_token: props.system.access_token,
            // eslint-disable-next-line @typescript-eslint/camelcase
            client_id: props.system.client_id,
            filial: props.system.consultorio
          }
        }
      );
    } catch (e) {
      props.updateWarning({
        text: 'Erro ao salvar a biometria. Entre em contato com o suporte',
        title: 'Erro',
        type: 'error'
      });
      props.setLoadingStatus(LoadingState.Failed);
      return;
    }
    props.updateWarning({
      text: 'Impressão digital salva com sucesso..',
      title: 'Salvo',
      type: 'success'
    });
    props.closeModal();
    props.setLoadingStatus(LoadingState.Success);
  };

  return (
    <ModalContainer
      key={1}
      showConsulta={false}
      showTelefone={false}
      onImgClick={() => null}
      icon={biometria}
      iconAlt='Biometria'
      title='Confirmação'
      show={props.show}
      desc={
        'Posicione o dedo indicador da mão direita no leitor e clique em Verificar para cadastrar sua biometria'
      }
      close={props.closeModal}
      leftBtn={{
        func: props.closeModal,
        title: 'Cancelar'
      }}
      rightBtn={{
        func: updateBio,
        title: 'Verificar'
      }}
    />
  );
};

const mapStateToProps = ({ paciente, system }: AppState) => ({
  paciente,
  system
});

const mapDispatchToProps = (dispatch) => ({
  setLoadingStatus: (status) =>
    dispatch(
      updateLoading(SystemActionTypes.UPDATE_LOADING, { loading: status })
    ),
  clearPaciente: () => dispatch(updatePaciente(PacienteActionTypes.CLEAR)),
  updateWarning: (warning: ModalWarning) =>
    dispatch(updateWarning(SystemActionTypes.UPDATE_WARNING, { warning }))
});

export default connect(mapStateToProps, mapDispatchToProps)(ModalBiometria);
