/* eslint-disable @typescript-eslint/camelcase */
import React, { useEffect } from 'react';
import './ModalTelefone.scss';
import sms from '../../../assets/icons/sms.png';
import { AppState } from '../../../configureStore';
import { connect } from 'react-redux';
import Input from '../../Input/Input';
import ModalContainer from '../ModalContainer/ModalContainer';
import {
  PacienteState,
  Telefone,
  TelefoneType,
  updatePaciente
} from '../../../stores/paciente';
import {
  updateLoading,
  ModalWarning,
  updateWarning,
  SystemState,
  LoadingState
} from '../../../stores/system';
import SystemActionTypes from '../../../stores/system/system.action-types';
import Axios, { AxiosResponse } from 'axios';
import { triggerHook } from '../../../webhook-logger';
import PacienteActionTypes from '../../../stores/paciente/paciente.action-types';

interface Props {
  closeModal(): any;
  show: boolean;
  paciente: PacienteState;
  setLoadingStatus(status: LoadingState);
  updateWarning(warning: ModalWarning);
  clearPaciente();
  system: SystemState;
}

const ModalTelefone: React.FC<Props> = (props) => {
  const inputRef = React.createRef<Input>();

  useEffect(() => {
    if (inputRef.current?.inputElRef?.current) {
      inputRef.current.inputElRef.current.value = String(
        props.paciente.telefones.find((telefone) => telefone.tipo === 'Celular')
          ?.telefone || props.paciente.telefones[0]?.telefone
      );
    }
  }, [inputRef.current]);

  const updatePhone = async () => {
    props.setLoadingStatus(LoadingState.Loading);
    const inputData = inputRef.current.inputElRef.current.value.replace(
      /\D/g,
      ''
    );
    let response: AxiosResponse<any>;
    try {
      const telefone: [Telefone] = [
        {
          telefone: inputData,
          telefone_sigla: TelefoneType.Celular
        }
      ];
      response = await Axios.put(
        `https://api-athenasaude.sensedia.com/beneficiarios-athena/v1/beneficiarios/${props.paciente.id_beneficiario}/telefones`,
        telefone,
        {
          headers: {
            // eslint-disable-next-line @typescript-eslint/camelcase
            access_token: props.system.access_token,
            // eslint-disable-next-line @typescript-eslint/camelcase
            client_id: props.system.client_id,
            filial: props.system.consultorio
          }
        }
      );
    } catch (e) {
      triggerHook(JSON.stringify(e) + JSON.stringify(response));
      props.updateWarning({
        text: 'Erro ao salvar dados. Entre em contato com o suporte',
        title: 'Erro',
        type: 'error'
      });
      props.setLoadingStatus(LoadingState.Failed);
      return;
    }
    props.updateWarning({
      text: 'Dados salvos.',
      title: 'Salvo',
      type: 'success'
    });
    props.closeModal();
    props.setLoadingStatus(LoadingState.Success);
  };

  return (
    <ModalContainer
      showConsulta={true}
      showTelefone={false}
      icon={sms}
      iconAlt='SMS'
      desc='Digite seu telefone abaixo'
      title='Alterar telefone'
      show={props.show}
      close={() => {
        props.closeModal();
      }}
      leftBtn={{
        func: props.closeModal,
        title: 'Cancelar'
      }}
      rightBtn={{
        func: updatePhone,
        title: 'Salvar'
      }}>
      <Input
        type='number'
        required={true}
        placeholder='Toque aqui para digitar'
        ref={inputRef}
        onChange={() => {
          if (inputRef.current.inputElRef.current.value.length === 11) {
            inputRef.current.inputElRef.current.blur();
          }
        }}
        keydown={(event) => (event.key === 'Enter' ? updatePhone() : null)}
      />
    </ModalContainer>
  );
};

const mapStateToProps = ({ paciente, system }: AppState) => ({
  paciente,
  system
});

const mapDispatchToProps = (dispatch) => ({
  setLoadingStatus: (status) =>
    dispatch(
      updateLoading(SystemActionTypes.UPDATE_LOADING, { loading: status })
    ),
  clearPaciente: () => dispatch(updatePaciente(PacienteActionTypes.CLEAR)),
  updateWarning: (warning: ModalWarning) =>
    dispatch(updateWarning(SystemActionTypes.UPDATE_WARNING, { warning }))
});

export default connect(mapStateToProps, mapDispatchToProps)(ModalTelefone);
