import React from 'react';
import './Identificacao.scss';
import PageContainer from '../../components/PageContainer/PageContainer';
import Button from '../../components/Button/Button';
import cpf from '../../assets/icons/cpf.png';
import cartao from '../../assets/icons/cartao convenio.png';
import ModalCPF from '../../components/modals/ModalCPF/ModalCPF';
import ModalCartaoConvenio from '../../components/modals/ModalCartaoConvenio/ModalCartaoConvenio';
import ModalBiometria from '../../components/modals/ModalBiometria/ModalBiometria';
import { LinkContext } from '../../routes';
import ModalTelefone from '../../components/modals/ModalTelefone/ModalTelefone';

export enum Modals {
  CPF = 0,
  CartaoConvenio,
  Telefone,
  Biometria
}

export const SSKey = 'IdentificacaoRedirect';

interface State {
  modal: Modals;
}

export default class Identificacao extends React.Component<{}, State> {
  static contextType = LinkContext;
  constructor(props) {
    super(props);
    const modal = sessionStorage.getItem(SSKey);
    this.state = { modal: modal ? Number(modal) : null };
    sessionStorage.removeItem(SSKey);
  }

  closeModal = () => this.setState({ modal: null });

  render() {
    return (
      <PageContainer title='Identificação' breadcrumbState={1}>
        <ModalCPF
          closeModal={this.closeModal}
          show={this.state.modal === Modals.CPF}
        />
        <ModalCartaoConvenio
          closeModal={this.closeModal}
          show={this.state.modal === Modals.CartaoConvenio}
        />
        <ModalBiometria
          closeModal={() => {
            this.closeModal();
            this.context.Opcoes.current.click();
          }}
          show={this.state.modal === Modals.Biometria}
        />
        <ModalTelefone
          closeModal={() => {
            this.closeModal();
            this.context.Opcoes.current.click();
          }}
          show={this.state.modal === Modals.Telefone}
        />
        <div className='Identificacao'>
          <h3>Identifique o paciente para prosseguir com o atendimento</h3>
          <h5>Selecione uma das opções abaixo:</h5>
          <Button
            key={1}
            icon={cpf}
            iconAlt='CPF'
            big={true}
            func={() => this.setState({ modal: Modals.CPF })}>
            Por CPF
          </Button>
          <Button
            key={2}
            icon={cartao}
            iconAlt='Cartao de Convenio'
            big={true}
            func={() => this.setState({ modal: Modals.CartaoConvenio })}>
            Cartão de Convênio
          </Button>
        </div>
      </PageContainer>
    );
  }
}
