import React from 'react';
import './Opcoes.scss';
import PageContainer from '../../components/PageContainer/PageContainer';
import Button from '../../components/Button/Button';
import mulher from '../../assets/icons/mulher.png';
import {
  LoadingState,
  updateLoading,
  ModalWarning,
  updateWarning
} from '../../stores/system';
import SystemActionTypes from '../../stores/system/system.action-types';
import { connect } from 'react-redux';
import Input from '../../components/Input/Input';
import { LinkContext } from '../../routes';
import biometria from '../../assets/icons/biometria.png';
import { SSKey, Modals } from '../Identificacao/Identificacao';

interface Button {
  name: string;
  desc: string;
  icon: 'schedule' | 'person';
  auth: boolean;
}

interface State {
  buttons: Button[];
  modalOpen: boolean;
}

interface Props {
  setLoadingStatus(status: LoadingState);
  updateWarning(warning: ModalWarning);
}

class Opcoes extends React.Component<Props, State> {
  static contextType = LinkContext;
  modalOpen = true;
  inputCodigo = React.createRef<Input>();

  constructor(props) {
    super(props);
    this.state = { buttons: [], modalOpen: false };
  }

  invertModal = () =>
    this.setState((prevState) => ({
      ...prevState,
      modalOpen: !prevState.modalOpen
    }));

  render() {
    return (
      <PageContainer title='Escolha a opção desejada' breadcrumbState={2}>
        <div className='Filas'>
          <h5>
            Clique em uma das opções abaixo para realizar alterações em seu
            cadastro
          </h5>
          <Button
            icon={biometria}
            iconAlt='Biometria'
            big={true}
            func={() => {
              sessionStorage.setItem(SSKey, String(Modals.Biometria));
              this.context.Identificacao.current.click();
            }}>
            Cadastrar biometria
          </Button>
          {/* <Button
            icon={mulher}
            iconAlt='Mulher'
            big={true}
            func={() => {
              sessionStorage.setItem(SSKey, String(Modals.ReconhecimentoFacial));
              this.context.Identificacao.current.click();
            }}>
            Cadastrar face
          </Button> */}
          <Button
            icon={mulher}
            iconAlt='Pessoa'
            big={true}
            func={() => {
              sessionStorage.setItem(SSKey, String(Modals.Telefone));
              this.context.Identificacao.current.click();
            }}>
            Alterar telefone
          </Button>
          {/* <h5 style={{ marginTop: '21px' }}>
            Selecione essa opção para falar com nossos atendentes:
          </h5>
          <Button
            icon={atendimento}
            iconAlt='Atendimento'
            big={true}
            func={() => {
              sessionStorage.removeItem(SSKey);
              this.context.Agendamentos.current.click();
            }}>
            Senha de atendimento
          </Button> */}
        </div>
      </PageContainer>
    );
  }
}

const mapDispatchToProps = (dispatch): Props => ({
  setLoadingStatus: (status) =>
    dispatch(
      updateLoading(SystemActionTypes.UPDATE_LOADING, { loading: status })
    ),
  updateWarning: (warning: ModalWarning) =>
    dispatch(updateWarning(SystemActionTypes.UPDATE_WARNING, { warning }))
});

export default connect(null, mapDispatchToProps)(Opcoes);
